@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
* {
	padding: 0%;
	margin: 0%;
	box-sizing: border-box;
	font-family: "Roboto", sans-serif;
}

.app {
	max-width: 1100px;
	margin-left: auto;
	margin-right: auto;
}

.details {
	padding: 20px 30px;
}
.heading {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;
	margin-bottom: 20px;
}
.heading h2 {
	color: #535252;
}
.heading h2 > span {
	font-size: 14px;
}
.heading img {
	height: 90px;
	width: auto;
}
.nodata {
	text-align: center;
	margin-top: 15vh;
	margin-bottom: 5vh;
	color: #818181;
}
table {
	font-family: arial, sans-serif;
	border-collapse: collapse;
	width: 100%;
}

td,
th {
	text-align: center;
	padding: 8px;
}

tr:nth-child(1) {
	background-color: #332272 !important;
	color: white;
}

tr:nth-child(even) {
	background-color: #ececec;
}

tr:nth-child(odd) {
	background-color: #f9f9f9;
}
.timing {
	padding-bottom: 5px;
}
.buttons {
	margin-left: 50%;
	-webkit-transform: translateX(-25%);
	        transform: translateX(-25%);
}
.back {
	background-color: #ff3838;
	color: white;
	font-size: 18px;
	border: none;
	height: 40px;
	width: 120px;
	border-radius: 5px;
}
.update {
	background-color: #332272;
	color: white;
	height: 40px;
	width: 120px;
	font-size: 18px;
	border-radius: 5px;
	border: none;
}

.homepage {
	display: -webkit-flex;
	display: flex;
	margin-top: 20%;
	border-radius: 10px;
	background-color: rgb(245, 245, 245);
}
img {
	width: 50%;
	border-radius: 10px 0px 0px 10px;
}
.right_content {
	position: relative;
	width: 100%;
}
.right_content img {
	position: absolute;
	right: 0;
	height: 80px;
	width: auto;
}
.right_content .form {
	margin-top: 100px;
	margin-left: 20px;
	margin-right: 20px;
}
.form label {
	font-size: 20px;
	color: #504d4d;
}
.form input {
	width: 100%;
	height: 50px;
	margin: 5px 0px 20px 0px;
	border-radius: 5px;
	border: none;
	padding: 10px 10px;
	background-color: #ececec;
}
.form .stats {
	background-color: #332272;
	color: white;
	font-size: 18px;
	border-radius: 5px;
	border: none;
}
.form button {
	border-radius: 5px;
	border: none;
	font-size: 18px;
	color: white;
	width: 100%;
	height: 50px;
	background-color: #ff3838;
}

